import {
  translateElement,
  bytes,
  timeLeft,
  copyToClipboard,
} from "../utils.mjs";
import storage from "../storage.mjs";
import { tooltip } from "../tooltip.mjs";

/**
 * <upload-right> - Right column component
 *
 * Responsibilities:
 * - Display intro text (upload-title template)
 * - OR display list of completed uploads
 * - Emit events: delete, copy (from upload list items)
 *
 * Fine-grained rendering:
 * - showIntro() / showUploadList() replace entire template
 * - addUpload(), removeUpload() update specific list items
 */
class UploadRightElement extends HTMLElement {
  constructor() {
    super();
    this.currentTemplate = null; // 'intro' or 'list'
    this.translate = null;

    // Cached element references
    this.elements = {
      uploadList: null,
    };

    this._afterPaintFrame = null;
  }

  connectedCallback() {
    // Get translate function from global state if available
    if (window.initialState && window.initialState.translate) {
      this.translate = window.initialState.translate;
    }

    // Show intro by default
    this.showIntro();
  }

  disconnectedCallback() {
    if (this._afterPaintFrame !== null) {
      cancelAnimationFrame(this._afterPaintFrame);
      this._afterPaintFrame = null;
    }
  }

  _renderTemplate(templateId, onReady) {
    const template = document.getElementById(templateId);
    if (!template) {
      console.error(`Template #${templateId} not found`);
      return;
    }

    const content = template.content.cloneNode(true);
    this.innerHTML = "";
    this.appendChild(content);

    if (this._afterPaintFrame !== null) {
      cancelAnimationFrame(this._afterPaintFrame);
    }

    this._afterPaintFrame = requestAnimationFrame(() => {
      this._afterPaintFrame = null;
      if (!this.isConnected) {
        return;
      }

      translateElement(this);

      if (typeof onReady === "function") {
        onReady();
      }
    });
  }

  /**
   * Template Switching Methods
   */

  /**
   * Show intro/welcome text
   * Uses template: #upload-title
   */
  showIntro() {
    this._renderTemplate("upload-title", () => {
      this.currentTemplate = "intro";
    });
  }

  /**
   * Show list of completed uploads
   * Uses template: #upload-list
   * @param {Array} files - Array of OwnedFile objects from storage
   */
  showUploadList(files = []) {
    this._renderTemplate("upload-list", () => {
      this.elements.uploadList = this.querySelector("#upload-list-container");
      this.currentTemplate = "list";

      // Populate with files
      if (files && files.length > 0) {
        // Filter expired and reverse (newest first)
        const activeFiles = files.filter((f) => !f.expired).reverse();
        activeFiles.forEach((file) => this.addUpload(file));
      }
    });
  }

  /**
   * Fine-Grained Update Methods
   */

  /**
   * Add a completed upload to the list (when in 'list' template)
   */
  addUpload(ownedFile) {
    if (this.currentTemplate !== "list" || !this.elements.uploadList) {
      console.warn("addUpload called but not in list template");
      return;
    }

    const itemTemplate = document.getElementById("upload-tile-item");
    if (!itemTemplate) {
      console.error("Template #upload-tile-item not found");
      return;
    }

    const item = itemTemplate.content.cloneNode(true);
    const li = item.querySelector("li");

    // Set upload ID
    li.setAttribute("data-upload-id", ownedFile.id);

    // Set file name
    const nameEl = item.querySelector('[data-role="file-name"]');
    if (nameEl) {
      nameEl.textContent = ownedFile.name;
    }

    // Set file size
    const sizeEl = item.querySelector('[data-role="file-size"]');
    if (sizeEl) {
      sizeEl.textContent = bytes(ownedFile.size);
    }

    // Set upload date
    const dateEl = item.querySelector('[data-role="upload-date"]');
    if (dateEl) {
      dateEl.textContent = this._formatDate(ownedFile.createdAt);
    }

    // Set expiry info
    const expiryEl = item.querySelector('[data-role="expiry-info"]');
    if (expiryEl) {
      expiryEl.innerHTML = this._createExpiryHTML(ownedFile);
    }

    // Set icon based on file type
    const iconEl = item.querySelector('[data-role="file-icon"]');
    if (iconEl && ownedFile.name) {
      const fileName = ownedFile.name.toLowerCase();
      if (fileName.endsWith(".zip")) {
        iconEl.className =
          "ri-folder-6-line h-8 w-8 flex-shrink-0 text-primary text-3xl leading-8";
      }
    }

    // Set file details (for multi-file uploads)
    const detailsEl = item.querySelector('[data-role="file-details"]');
    if (
      detailsEl &&
      ownedFile.manifest &&
      ownedFile.manifest.files &&
      ownedFile.manifest.files.length > 1
    ) {
      detailsEl.innerHTML = this._createFileDetailsHTML(
        ownedFile.manifest.files,
      );
    }

    // Set download link
    const downloadLink = item.querySelector('[data-role="download-link"]');
    if (downloadLink) {
      downloadLink.href = ownedFile.url;
    }

    // Set owner/recipient information
    const ownershipEl = item.querySelector('[data-role="file-ownership"]');
    if (ownershipEl) {
      ownershipEl.innerHTML = this._createOwnershipHTML(ownedFile);
    }

    // Attach event listeners
    const deleteBtn = item.querySelector('[data-action="delete"]');
    if (deleteBtn) {
      deleteBtn.addEventListener("click", (e) =>
        this._handleDeleteClick(e, ownedFile),
      );
    }

    const copyBtn = item.querySelector('[data-action="copy"]');
    if (copyBtn) {
      copyBtn.addEventListener("click", (e) =>
        this._handleCopyClick(e, ownedFile.url),
      );
    }

    const logsBtn = item.querySelector('[data-action="logs"]');
    if (logsBtn) {
      logsBtn.addEventListener("click", (e) =>
        this._handleLogsClick(e, ownedFile),
      );
    }

    const editBtn = item.querySelector('[data-action="edit"]');
    if (editBtn) {
      editBtn.addEventListener("click", (e) =>
        this._handleEditClick(e, ownedFile),
      );
    }

    // Hide copy button for files with recipients and hide recipient notice (now a tooltip)
    const recipientNotice = item.querySelector('[data-role="recipient-notice"]');
    if (ownedFile.recipients && ownedFile.recipients.length > 0) {
      // File has recipients - hide copy button
      if (copyBtn) {
        copyBtn.classList.add("hidden");
      }
      // Hide recipient notice div (now displayed as tooltip)
      if (recipientNotice) {
        recipientNotice.classList.add("hidden");
      }
    } else {
      // No recipients - keep copy button visible, hide notice
      if (recipientNotice) {
        recipientNotice.classList.add("hidden");
      }
    }

    console.log(ownedFile);

    // Create Sharing Hints
    if (ownedFile.recipients && ownedFile.recipients.length > 0) {
      const secure_notice = item.querySelector('[data-role="file-is-secure"]');
      if (secure_notice) {
        secure_notice.classList.remove("hidden");
        // Add tooltip with recipient login message
        const tooltipText = this._translateText(
          "fileTileRecipientNotice",
          "The recipient must login to download this file"
        );
        tooltip(secure_notice, tooltipText, {
          position: "top",
          default: "closed",
        });
      }
    } else {
      console.log("No recipients");
      if (ownedFile._hasPassword) {
        console.log("Has password");
        const sharable_notice = item.querySelector(
          '[data-role="file-is-sharable"]',
        );
        if (sharable_notice) {
          sharable_notice.classList.remove("hidden");
          // Add tooltip for password sharing recommendation
          const tooltipText = this._translateText(
            "fileTileSharableNotice",
            "Share this link and the password over two different channels (eg. eMail, encrypted Messenger)"
          );
          tooltip(sharable_notice, tooltipText, {
            position: "top",
            default: "closed",
          });
        }
      } else {
        console.log("Has no password");
        const unshareable_notice = item.querySelector(
          '[data-role="file-is-unshareable"]',
        );
        if (unshareable_notice) {
          unshareable_notice.classList.remove("hidden");
          // Add tooltip for secret link warning
          const tooltipText = this._translateText(
            "fileTileUnshareableNotice",
            "The link is secret. It is inadvisable to share the link over any unencrypted channel (eg. eMail)"
          );
          tooltip(unshareable_notice, tooltipText, {
            position: "top",
            default: "closed",
          });
        }
      }
    }

    // Create Password Hints
    const passwort_hint = item.querySelector(
      '[data-role="file-is-pwd-protected"]',
    );
    if (passwort_hint && ownedFile.hasPassword) {
      passwort_hint.classList.remove("hidden");
      // Add tooltip for password protected indicator
      const tooltipText = this._translateText(
        "fileTilePwdProtectedNotice",
        "File is password protected"
      );
      tooltip(passwort_hint, tooltipText, {
        position: "top",
        default: "closed",
      });
    }

    // Prepend to list (newest first)
    this.elements.uploadList.prepend(li);

    // Translate any translatable elements
    requestAnimationFrame(() => {
      translateElement(li);
    });
  }

  /**
   * Remove an upload from the list (when in 'list' template)
   */
  removeUpload(fileId) {
    if (this.currentTemplate !== "list" || !this.elements.uploadList) {
      console.warn("removeUpload called but not in list template");
      return;
    }

    const uploadElement = this.querySelector(`[data-upload-id="${fileId}"]`);
    if (uploadElement) {
      uploadElement.remove();
    }

    // If list is now empty, switch back to intro
    if (this.elements.uploadList.children.length === 0) {
      this.showIntro();
    }
  }

  /**
   * Refresh entire list with new files
   */
  refreshList(files) {
    if (this.currentTemplate !== "list") {
      return;
    }

    // Clear list
    if (this.elements.uploadList) {
      this.elements.uploadList.innerHTML = "";
    }

    // Filter expired and reverse
    const activeFiles = files.filter((f) => !f.expired).reverse();

    if (activeFiles.length === 0) {
      this.showIntro();
      return;
    }

    activeFiles.forEach((file) => this.addUpload(file));
  }

  /**
   * Helper Methods
   */

  _createExpiryHTML(ownedFile) {
    const downloadsLeft = ownedFile.dlimit - ownedFile.dtotal;
    const timeLeftMs = ownedFile.expiresAt - Date.now();

    if (timeLeftMs < 0 || downloadsLeft <= 0) {
      return this._translateText("linkExpiredAlt", "Link expired");
    }

    const timeL10n = timeLeft(timeLeftMs);
    const downloadCountText = this._translateText(
      "downloadCount",
      `${downloadsLeft} downloads`,
      { num: downloadsLeft },
    );
    const timespanText = this._translateText(
      timeL10n.id,
      this._formatTimeLeft(timeLeftMs),
      timeL10n,
    );

    return this._translateText(
      "archiveExpiryInfo",
      `Expires after ${downloadCountText} or ${timespanText}`,
      { downloadCount: downloadCountText, timespan: timespanText },
    );
  }

  _createFileDetailsHTML(files) {
    const fileCount = this._translateText(
      "fileCount",
      `${files.length} files`,
      { num: files.length },
    );

    // Clone the details template
    const detailsTemplate = document.getElementById("upload-file-details");
    if (!detailsTemplate) {
      console.error("Template #upload-file-details not found");
      return "";
    }

    const detailsFragment = detailsTemplate.content.cloneNode(true);
    const details = detailsFragment.querySelector("details");

    // Set file count
    const fileCountEl = details.querySelector('[data-role="file-count"]');
    if (fileCountEl) {
      fileCountEl.textContent = fileCount;
    }

    // Get the file list container
    const fileListEl = details.querySelector('[data-role="file-list"]');
    if (!fileListEl) {
      console.error("File list container not found");
      return details.outerHTML;
    }

    // Clone file item template for each file
    const itemTemplate = document.getElementById("upload-file-details-item");
    if (!itemTemplate) {
      console.error("Template #upload-file-details-item not found");
      return details.outerHTML;
    }

    files.forEach((f) => {
      const itemFragment = itemTemplate.content.cloneNode(true);

      const nameEl = itemFragment.querySelector('[data-role="item-name"]');
      if (nameEl) {
        nameEl.textContent = f.name;
      }

      const sizeEl = itemFragment.querySelector('[data-role="item-size"]');
      if (sizeEl) {
        sizeEl.textContent = bytes(f.size);
      }

      // Set icon based on file type
      const iconEl = itemFragment.querySelector('[data-role="file-icon"]');
      if (iconEl && f.name) {
        const fileName = f.name.toLowerCase();
        if (fileName.endsWith(".zip")) {
          iconEl.className =
            "ri-folder-6-line h-6 w-6 text-primary flex-shrink-0 text-2xl leading-6";
        }
      }

      fileListEl.appendChild(itemFragment);
    });

    return details.outerHTML;
  }

  _formatTimeLeft(milliseconds) {
    const seconds = Math.floor(milliseconds / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);

    if (days >= 1) {
      return `${days} day${days !== 1 ? "s" : ""}`;
    } else if (hours >= 1) {
      return `${hours} hour${hours !== 1 ? "s" : ""}`;
    } else if (minutes >= 1) {
      return `${minutes} minute${minutes !== 1 ? "s" : ""}`;
    }
    return "< 1 minute";
  }

  _formatDate(timestamp) {
    if (!timestamp) {
      return "";
    }

    const date = new Date(timestamp);
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const uploadDate = new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate(),
    );

    // Get user's locale from document or default to browser locale
    const locale = document.documentElement.lang || navigator.language || "en";

    // If uploaded today, show time only
    if (uploadDate.getTime() === today.getTime()) {
      return new Intl.DateTimeFormat(locale, {
        hour: "2-digit",
        minute: "2-digit",
      }).format(date);
    }

    // If uploaded this year, show month and day
    if (date.getFullYear() === now.getFullYear()) {
      return new Intl.DateTimeFormat(locale, {
        month: "short",
        day: "numeric",
      }).format(date);
    }

    // Otherwise, show full date with year
    return new Intl.DateTimeFormat(locale, {
      year: "numeric",
      month: "short",
      day: "numeric",
    }).format(date);
  }

  _translateText(key, fallback, args = {}) {
    // Try instance translate function first
    if (this.translate) {
      try {
        return this.translate(key, args);
      } catch (e) {
        // Fall through to window.translate
      }
    }

    // Fall back to global translate function
    if (window.translate) {
      try {
        return window.translate(key, args);
      } catch (e) {
        // Fall through to fallback
      }
    }

    return fallback;
  }

  _getCurrentUserName() {
    // Get current user's name from storage.user
    if (storage && storage.user && storage.user.name) {
      return storage.user.name.toLowerCase();
    }
    return null;
  }

  _createOwnershipHTML(ownedFile) {
    const parts = [];
    const currentUserName = this._getCurrentUserName();

    // Show FROM if we have owner information and it's not the current user
    if (ownedFile.ownerString) {
      const isCurrentUser =
        currentUserName &&
        ownedFile.ownerString.toLowerCase() === currentUserName.toLowerCase();

      if (!isCurrentUser) {
        const fromLabel = this._translateText("fileTileFrom", "FROM");
        parts.push(
          `<span class="inline-flex items-center px-2 py-1 rounded bg-primary/10 dark:bg-primary/20 text-primary text-xs">${fromLabel}: ${ownedFile.ownerString}</span>`,
        );
      }
    } else if (ownedFile.authString) {
      // Guest upload via auth link
      const fromLabel = this._translateText("fileTileFrom", "FROM");
      const guestLabel = this._translateText("fileTileGuest", "Guest");
      parts.push(
        `<span class="inline-flex items-center px-2 py-1 rounded bg-primary/10 dark:bg-primary/20 text-primary text-xs">${fromLabel}: ${ownedFile.authString}</span>` +
          `<span class="inline-flex items-center px-2 py-1 rounded bg-grey-20 dark:bg-grey-80 text-xs normal-case">${guestLabel}</span>`,
      );
    }

    // Show TO if recipients exist (filter out current user)
    if (
      Array.isArray(ownedFile.recipients) &&
      ownedFile.recipients.length > 0
    ) {
      const toLabel = this._translateText("fileTileTo", "TO");

      // Filter out current user from recipients
      const otherRecipients = ownedFile.recipients.filter((r) => {
        if (!currentUserName || !r.userName) return true;
        return r.userName.toLowerCase() !== currentUserName.toLowerCase();
      });

      // Display each recipient
      otherRecipients.forEach((recipient) => {
        const recipientName =
          recipient.userName ||
          recipient.userEmail ||
          `User ${recipient.userId}`;
        parts.push(
          `<span class="inline-flex items-center px-2 py-1 rounded bg-secondary/10 dark:bg-secondary/20 text-secondary text-xs">${toLabel}: ${recipientName}</span>`,
        );
      });
    }

    return parts.join("");
  }

  /**
   * Event Handlers
   */

  _handleCopyClick(event, url) {
    event.preventDefault();
    event.stopPropagation();

    const success = copyToClipboard(url);

    if (success) {
      // Dispatch copy event to bubble up
      this.dispatchEvent(
        new CustomEvent("copy", {
          bubbles: true,
          detail: { url },
        }),
      );

      // Visual feedback - change icon to check
      const copyBtn = event.currentTarget;
      const iconEl = copyBtn.querySelector('[data-role="copy-icon"]');
      const textSpan = copyBtn.querySelector('[id="copyLinkButton"]');

      if (iconEl) {
        const originalClass = iconEl.className;
        iconEl.className = "ri-check-line h-4 w-4 mr-2 text-base leading-4";
        setTimeout(() => {
          iconEl.className = originalClass;
        }, 1000);
      }

      if (textSpan) {
        const originalText = textSpan.textContent;
        textSpan.textContent = this._translateText("copiedUrl", "Copied!");
        setTimeout(() => {
          textSpan.textContent = originalText;
        }, 1000);
      }
    }
  }

  _handleDeleteClick(event, ownedFile) {
    event.preventDefault();
    event.stopPropagation();

    // Dispatch delete event to bubble up to controller
    this.dispatchEvent(
      new CustomEvent("delete", {
        bubbles: true,
        detail: { ownedFile },
      }),
    );
  }

  _handleLogsClick(event, ownedFile) {
    event.preventDefault();
    event.stopPropagation();

    // Dispatch event to parent layout to show full-width logs view
    this.dispatchEvent(
      new CustomEvent("show-file-logs", {
        bubbles: true,
        detail: { fileName: ownedFile.name, fileId: ownedFile.id },
      }),
    );
  }

  _handleEditClick(event, ownedFile) {
    event.preventDefault();
    event.stopPropagation();

    // Dispatch event to parent layout to show edit view
    this.dispatchEvent(
      new CustomEvent("edit-file", {
        bubbles: true,
        detail: { ownedFile },
      }),
    );
  }
}

// Register the custom element
customElements.define("upload-right", UploadRightElement);
